
import { inject } from "vue";
import City from "@/components/CityPage/index.vue";
export default {
  name: "city",
  components: { City },
  setup() {
    const setToolbarState: any = inject("setToolbarState");
    setToolbarState({
      routeControl: true,
      rotateControl: true,
      initControl: true,
      heatmapControl: false,
      flyLineControl: false,
      featurePointControl: false,
      videoFusion: false,
    });
  },
};
